@import '../../constants/constants.module';
.Wrapper {
  width: 100%;
  margin-bottom: 200px;
  display: flex;
  flex-direction: column;
  overflow: hidden;

  form {
    display: flex;
    flex-direction: column;
    .InputWrapper {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
    }
    .subWrapper {
      display: flex;
      flex-wrap: wrap;
      @media (max-width: $mobile) {
        justify-content: space-around;
      }

      &:nth-last-child(2) {
        justify-content: flex-start;
        @media (max-width: $mobile) {
          justify-content: space-around;
        }
      }
      &:nth-last-child(1) {
        justify-content: flex-start;
        @media (max-width: $mobile) {
          justify-content: space-around;
        }
      }
    }

    .fieldWrapper {
      position: relative;

      .field {
        margin-bottom: 25px;
        width: 150px;
        padding: 10px 25px 10px 5px;
        border: 1px solid $black;
        margin-right: 40px;
        &::placeholder {
          color: $black;
        }
        @media (max-width: $mobile) {
          width: 280px;
          margin-right: 0;
        }
      }
      .errMsg {
        color: red;
        font-weight: $bold;
        position: absolute;
        top: 40px;
        width: 125px;
        font-size: $MTextFont - 0.2rem;
        padding-left: 5px;
        z-index: 2;
      }
    }
    .submitBtn {
      width: 200px;
      cursor: pointer;
      background-color: $black;
      color: $white;
      font-weight: $bold;
      border: none;
      padding: 10px 25px;
      transition: 0.3s ease-in-out;
      @media (max-width: $mobile) {
        margin: 0 auto;
      }

      &:hover {
        background-color: $white;
        color: green;
      }
    }
  }
}
